<template>
  <div class="mobileLogIn">
    <!--  -->
    <div class="row align-items-center bg-light pt-5 pb-3 ps-3 pe-3 navbar fixed-top">
      <div class="container">
        <div class="col-2">
          <a href="javascript:history.back()"><i class="fa-solid fa-chevron-left"></i></a>
        </div>
        <div class="col-8"></div>
        <div class="col-2"></div>
      </div>
    </div>
    <div class="body">
      <div class="card mx-3 py-5 mt-4 bg-light">
        <h1></h1>
        <div class="logInForm px-3 pt-5">
          <form action="">
            <div class="row g-0 ">
              <div class="col-3">
                <label for="mobile">:</label>
              </div>
              <div class="col-8">
                <input type="text" placeholder="" id="mobile" v-model="mobile" style="width:100%">
              </div>
            </div>
            <div class="row g-0 pt-4">
              <div class="col-3">
                <label for="code">:</label>
              </div>
              <div class="col-8">
                <input type="text" placeholder="" id="code" v-model="code" @keyup.enter="getLogIn" style="width:100%">
              </div>
            </div>
          </form>

          <div class="row mx-3 py-5 g-2">
            <button class="btn btn-primary" @click="getCode"> </button>
            <button class="btn btn-primary" @click="logInWithCode"> </button>
          </div>
        </div>

        <div class="iconList mt-5 pt-5 mb-5">
          <i class="fa-brands fa-qq fa-2x mx-2"></i>
          <i class="fa-brands fa-weibo fa-2x mx-2"></i>
          <i class="fa-brands fa-apple fa-2x mx-2"></i>
          <br>
          <span class="text-muted"></span>
        </div>
      </div>
    </div>
  </div>
</template>
<!-- axios -->
<script src="https://unpkg.com/axios/dist/axios.min.js"></script>
<script>
export default {
  name: 'MobileLogIn',
  components: {

  },
  mixins: [],
  props: {

  },
  data () {
    return {
      mobile: '',
      code: '',
      userId: '',
      isLogin: false
    }
  },
  computed: {

  },
  watch: {

  },
  mounted () {

  },
  methods: {
    getCode: function () {
      axios.get('http://46.101.60.239:3001/captcha/sent?phone=' + this.mobile).then
      (function (response) {
        console.log(response)
      }, function (err) {
        console.log(err)
      })
    },

    logInWithCode: function () {
      var that = this
      axios.get('http://46.101.60.239:3001/login/cellphone?phone=' + this.mobile + '&password=' + '&captcha=' + this.code).then
      (function (response) {
        if (response.status === 200) {
          window.sessionStorage.setItem('userId', response.data.account.id)
          window.sessionStorage.setItem('userCookie', encodeURIComponent(response.data.cookie))
          that.goIndex()
        }
      }, function (err) {
        console.log(err)
      })
    },

    goIndex: function () {
      this.$router.push({path: '/'})
      if (this.$route.fullPath == '/') {
        window.location.reload()
      }
    }

  }
}
</script>
<style lang='' scoped>
  .body{
    padding-top: 100px;
    padding-bottom: 60px;
  }

</style>



